
import url from "../../bin/url";
import XTextarea from 'vux/src/components/x-textarea/index.vue'

export default {
components: {
XTextarea
},
data() {
return {
key: "value",
selectAddressN: "",
postFee:'',
form:{
openId:"",
addressId:null,
remarks:"",
goodList:[],
id:null,
num:null,
specList:[
{
specId:null,
specName:"",
value:null,
}
]
},
goodsMsg: {}
};
},
computed:{
option(){
return JSON.parse(this.$route.query.data);
},
count(){
return this.$route.query.count
}
},
methods: {
goAddress() {
this.$router.push("/addressment");
},

goList() {
this.$router.push("/goodslist");
},




getSelectAddress(item) {

let _obj = {
openId: localStorage.getItem("openId"),
id: item
};
this.$fetch.post(url.selectAddress, _obj).then(
data => {
if (data.code == 0) {
this.selectAddressN = data.obj;
}
})
}
},
mounted() {
this.$fetch.post("fruits/app/cart/getDefaultAddr",this.option).then(res =>{
console.log(res.obj);
this.goodsMsg = res.obj;
this.postFee = res.attributes.postage;
})
},
created() {
if (this.$route.params.obj) {
this.routeParams = JSON.parse(this.$route.params.obj);
this.getSelectAddress(this.routeParams.data.item); 
}
}




}






